import request from '@/apis/request.wms'

// 列表
export function getListApi(data) {
  return request({
    url: '/basic/factory/pageList',
    method: 'post',
    data
  })
}
// 下拉列表
export function getPullListItemApi(data) {
  return request({
    url: '/basic/factory/listItem',
    method: 'post',
    data
  })
}
// 新增
export function getAddApi(data) {
  return request({
    url: '/basic/factory/add',
    method: 'post',
    data
  })
}
// 修改
export function getEditApi(data) {
  return request({
    url: '/basic/factory/edit',
    method: 'post',
    data
  })
}
// 删除
export function getDeleteApi(data) {
  return request({
    url: '/basic/factory/delete',
    method: 'post',
    data
  })
}

